// frontend/src/components/TransferRequestList.tsx

import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { TransferRequest } from '../types/transfer';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const TransferRequestList: React.FC = () => {
    const [requests, setRequests] = useState<TransferRequest[]>([]);
    const token = localStorage.getItem('authToken');

    const fetchRequests = async () => {
        try {
            const response = await axios.get(`${API_BASE_URL}/transfers?status=PENDING`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setRequests(response.data);
        } catch (err) {
            console.error("Error fetching transfer requests", err);
        }
    };

    useEffect(() => {
        fetchRequests();
        const interval = setInterval(fetchRequests, 30000); // Auto-refresh every 30s
        return () => clearInterval(interval);
    }, []);

    const handleAction = async (id: number, newStatus: 'APPROVED' | 'REJECTED') => {
        try {
            await axios.patch(`${API_BASE_URL}/transfers/${id}`, 
                { new_status: newStatus },
                { headers: { Authorization: `Bearer ${token}` } }
            );
            fetchRequests(); // Refresh list
        } catch (err) {
            alert("Action failed");
        }
    };

    return (
        <div className="bg-white p-6 rounded-lg shadow-md border border-gray-200 mt-6">
            <h2 className="text-xl font-bold mb-4">Pending Transfer Requests</h2>
            <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                        <tr>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Patient</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Priority</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Need</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Action</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                        {requests.map((req) => (
                            <tr key={req.request_id} className={req.priority === 'Emergency' ? 'bg-red-50' : ''}>
                                <td className="px-4 py-3 text-sm font-medium text-gray-900">{req.patient_name}</td>
                                <td className={`px-4 py-3 text-sm font-bold ${req.priority === 'Emergency' ? 'text-red-600' : 'text-gray-600'}`}>
                                    {req.priority}
                                </td>
                                <td className="px-4 py-3 text-sm text-gray-500">{req.required_specialty}</td>
                                <td className="px-4 py-3 text-sm space-x-2">
                                    <button 
                                        onClick={() => req.request_id && handleAction(req.request_id, 'APPROVED')}
                                        className="text-green-600 hover:text-green-900 font-bold">Approve</button>
                                    <button 
                                        onClick={() => req.request_id && handleAction(req.request_id, 'REJECTED')}
                                        className="text-red-600 hover:text-red-900">Reject</button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
                {requests.length === 0 && <p className="text-center py-4 text-gray-500">No pending transfers.</p>}
            </div>
        </div>
    );
};

export default TransferRequestList;