// frontend/src/pages/DashboardPage.tsx

import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import BedCard from '../components/BedCard';
import TransferRequestForm from '../components/TransferRequestForm';
import TransferRequestList from '../components/TransferRequestList';

// --- Configuration ---
const API_BASE_URL = 'http://localhost:3000/api/v1';

const SPECIALTY_OPTIONS = [
    'All', 'General', 'ICU', 'Pediatric', 'HDU', 
    'CCU', 'NICU', 'Emergency beds', 'PACU', 'PICU'
];

const DashboardPage: React.FC = () => {
    const [beds, setBeds] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [selectedSpecialty, setSelectedSpecialty] = useState('All');
    
    const token = localStorage.getItem('authToken'); 

    const fetchBedData = useCallback(async (specialty: string) => {
        setIsLoading(true);
        setError(null);
        
        try {
            let url = `${API_BASE_URL}/beds`;
            if (specialty && specialty !== 'All') {
                url += `?specialty_type=${specialty}`;
            }

            const response = await axios.get(url, {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            
            setBeds(response.data);
        } catch (err: any) {
            console.error('Failed to fetch bed data:', err);
            if (err.response?.status === 401) {
                setError("Session expired. Please log in again.");
            } else {
                 setError('Could not load bed data. Please check the API server.');
            }
        } finally {
            setIsLoading(false);
        }
    }, [token]);

    useEffect(() => {
        fetchBedData(selectedSpecialty);
    }, [selectedSpecialty, fetchBedData]);

    const handleFilterChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
        setSelectedSpecialty(event.target.value);
    };

    if (error) return (
        <div className="flex items-center justify-center h-screen">
            <div className="bg-red-50 p-6 rounded-lg text-red-700 border border-red-200">
                <p className="font-bold">Error: {error}</p>
                <button onClick={() => window.location.href = '/'} className="mt-2 underline">Go to Login</button>
            </div>
        </div>
    );

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <header className="bg-indigo-800 text-white p-4 shadow-lg flex justify-between items-center">
                <h1 className="text-2xl font-bold flex items-center">
                    <span className="mr-2">üè•</span> No Bed Syndrome Command Center
                </h1>
                <div className="flex items-center space-x-4">
                    <span className="text-sm bg-indigo-700 px-3 py-1 rounded-full">Staff Mode</span>
                    <button 
                        onClick={() => { localStorage.clear(); window.location.href = '/'; }}
                        className="text-sm hover:text-red-200 transition"
                    >Logout</button>
                </div>
            </header>

            {/* Main Content Layout */}
            <main className="p-6 max-w-[1600px] mx-auto grid grid-cols-1 xl:grid-cols-12 gap-6">
                
                {/* LEFT SIDE: Bed Management (8 Columns) */}
                <div className="xl:col-span-8 space-y-6">
                    <section className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                        <div className="flex flex-col md:flex-row justify-between items-md-center mb-6 gap-4">
                            <div>
                                <h2 className="text-xl font-bold text-gray-800">Bed Inventory</h2>
                                <p className="text-sm text-gray-500">Real-time status of clinical beds</p>
                            </div>

                            <div className="flex items-center space-x-2">
                                <label htmlFor="specialty-filter" className="text-sm font-medium text-gray-600">Specialty:</label>
                                <select 
                                    id="specialty-filter"
                                    value={selectedSpecialty}
                                    onChange={handleFilterChange}
                                    className="p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-indigo-500 outline-none"
                                >
                                    {SPECIALTY_OPTIONS.map(opt => <option key={opt} value={opt}>{opt}</option>)}
                                </select>
                            </div>
                        </div>

                        {isLoading ? (
                            <div className="flex justify-center py-20 text-indigo-600 animate-pulse">Updating Inventory...</div>
                        ) : beds.length > 0 ? (
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                                {beds.map((bed: any) => (
                                    <BedCard 
                                        key={bed.bed_id} 
                                        bedData={bed} 
                                        onStatusChange={() => fetchBedData(selectedSpecialty)}
                                        token={token || ''}
                                    />
                                ))}
                            </div>
                        ) : (
                            <div className="text-center p-20 bg-gray-50 rounded-xl border-2 border-dashed border-gray-200">
                                <p className="text-gray-400">No beds found for "{selectedSpecialty}"</p>
                            </div>
                        )}
                    </section>
                </div>

                {/* RIGHT SIDE: Patient Flow (4 Columns) */}
                <div className="xl:col-span-4 space-y-6">
                    {/* Module 2: Create Request Form */}
                    <TransferRequestForm />

                    {/* Module 2: Monitoring List */}
                    <TransferRequestList />
                </div>
            </main>
        </div>
    );
};

export default DashboardPage;